import React, { Component } from 'react';
import Header from '../../components/Header/Header';
import PreSaleHorsePermanentTravelPermitFormContainer from '../../containers/PreSaleHorsePermanentTravelPermitFormContainer';
class PreSaleHorsePermanentTravelPermit extends Component {
    constructor() {
        super();
        this.state = {
          open:false,
        };
    }
    handleOpenNavBar = () => {
        this.setState({open:!this.state.open})
    }
    render() {
        return (
            <div>
                <div>
                    <div id="wrapper" className="wrapper">
                        <div id="st-container" className={this.state.open ? "st-container st-effect-1 st-menu-open" : "wrapper dashboard-wrapper st-container"}>
                            <Header
                                header="Pre Sale Horse Permanent Travel Permit"
                                openNavBar={this.handleOpenNavBar}
                                open={this.state.open} />
                            <div className="main-middle-area">
                                <section className="horse-pre-sale-section">
                                    <div className="horse-pre-sale-inner">
                                        <div className="container">
                                            <PreSaleHorsePermanentTravelPermitFormContainer />
                                        </div>
                                    </div>
                                </section>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default PreSaleHorsePermanentTravelPermit;