import React, { Component } from 'react'
import DropDownList from '../ui/DropDownList';
import InputField from '../ui/InputField';
import { getUrl } from '../../services/network/urls';
import { post, get } from '../../services/network/requests';
import "flatpickr/dist/themes/material_blue.css";
import '../../assets/css/cattle-form.css'
import Flatpickr from "react-flatpickr";
import SignatureCanvas from 'react-signature-canvas'
import _ from 'lodash';
import {Link} from 'react-router-dom'
export default class PreSaleHorsePermanentTravelPermitForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            breed: [
                { value: 0, label: 'breed1', type: 'breed' },
                { value: 1, label: 'breed2', type: 'breed' },
            ],
            color: [
                { value: 0, label: 'color1', type: 'color' },
                { value: 1, label: 'color2', type: 'color' },
            ],
            brand: [
                { value: 0, label: 'brand1', type: 'brand' },
                { value: 1, label: 'brand2', type: 'brand' },
            ],
            sex: [
                { value: 0, label: 'Male', type: 'sex' },
                { value: 1, label: 'Female', type: 'sex' },
            ],
            location: [
                { value: 0, label: 'location1', type: 'location' },
                { value: 1, label: 'location2', type: 'location' },
            ],
            city: [
                { value: 0, label: 'city1', type: 'city' },
                { value: 1, label: 'city2', type: 'city' },
            ],
            stateList: [
                { value: 0, label: 'State1', type: 'State' },
                { value: 1, label: 'State2', type: 'State' },
            ],
            facemarking: [
                { value: 0, label: 'left', type: 'Left' },
                { value: 1, label: 'right', type: 'Right' },
            ],
            markingLocation: [
                { value: 0, label: 'left', type: 'Left' },
                { value: 1, label: 'right', type: 'Right' },
            ],
            optionAge: [
                { value: '0-5', label: '0-5', type: '0-5' },
                { value: '5-10', label: '5-10', type: '5-10' },
                { value: '10+', label: '10+', type: '10+' },
            ],
            selectedBreed: '',
            selectedColor: '',
            selectedBrand: '',
            selectedSex: '',
            selectedFaceMarking: '',
            selectedMarkingLocation: '',
            otherMarkingLocation: '',
            selectedLocation: '',
            selectedCity: '',
            age: '',
            name: '',
            ownerName: '',
            ownerAddress: '',
            zipCode: '',
            phone: '',
            inspectDate: '',
            sellerSignatureDate: '',
            ownerSignatureDate: '',
            isPermited: false,
            brandInspectorSign: '',
            ownerSign: '',
            sellerOrAgentSign: '',
            clearance: '',
            inspectDateError: '',
            sellerSignatureDateError: '',
            ownerSignatureDateError: '',
            selectedBreedError: '',
            selectedColorError: '',
            selectedBrandError: '',
            selectedSexError: '',
            selectedFaceMarkingError: '',
            selectedMarkingLocationError: '',
            selectedLocationError: '',
            selectedCityError: '',
            nameError: '',
            ownerNameError: '',
            ownerAddressError: '',
            zipCodeError: '',
            phoneError: '',
            clearanceError: '',
            isPermitedError: false,
            activeSuggestionOwner: false,
            filteredSuggestions: [],
            ownerData: [],
            invalidImage: '',
            drawImage: []

        };

    }
    componentDidMount() {
        this.getAutoCompleteData();
    };

    getAutoCompleteData = () => {
        
        const url = getUrl("stockman_init");
        get(url)
            .then(res => {
                const { data: { code, data, status } } = res;
                switch (code) {
                    case 200:
                        if (status) {
                            this.setState({ ownerData: data.owner })
                        }
                        break;

                    default:
                        break;
                }
            })
            .catch(error => console.log(error))
    }


    handleChangeBreed = (selected) => {
        this.setState({ selectedBreed: selected }, this.handleChange('selectedBreed', selected))
    }
    handleChangeColor = (selected) => {
        this.setState({ selectedColor: selected }, this.handleChange('selectedColor', selected))
    }
    handleChangeBrand = (selected) => {
        this.setState({ selectedBrand: selected }, this.handleChange('selectedBrand', selected))
    }
    handleChangeSex = (selected) => {
        this.setState({ selectedSex: selected }, this.handleChange('selectedSex', selected))
    }
    handleChangeSelectedFaceMarking = (selected) => {
        this.setState({ selectedFaceMarking: selected }, this.handleChange('selectedFaceMarking', selected))
    }
    handleChangeSelectedMarkingLocation = (selected) => {
        this.setState({ selectedMarkingLocation: selected }, this.handleChange('selectedMarkingLocation', selected))
    }
    handleChangeOtherMarkingLocation = (selected) => {
        this.setState({ otherMarkingLocation: selected }, this.handleChange('otherMarkingLocation', selected))
    }
    handleChangeLocation = (selected) => {
        this.setState({ selectedLocation: selected }, this.handleChange('selectedLocation', selected))
    }
    handleChangeCity = (selected) => {
        this.setState({ selectedCity: selected }, this.handleChange('selectedCity', selected))
    }
    handleChangeState = (selected) => {
        this.setState({ selectedState: selected }, this.handleChange('selectedState', selected))
    }
    handleChangeAge = (selected) => {
        this.setState({ age: selected }, this.handleChange('age', selected))
    }

    handleChange = (name, value) => {
        switch (name) {
            case 'selectedBreed':
            case 'selectedBrand':
            case 'selectedSex':
            case 'selectedColor':
            case 'age':
            case 'selectedFaceMarking':
            case 'selectedMarkingLocation':
            case 'otherMarkingLocation':
            case 'selectedLocation':
            case 'selectedCity':
            case 'selectedState':
                if (value === '' || value === undefined) {
                    this.setState({ [name + 'Error']: _.startCase(name) + " is requied" })
                } else {
                    this.setState({ [name + 'Error']: '' })
                }
                break;
            default:
                break;
        }
    }

    onChangeChecked = (e) => {
        const { checked, name } = e.target;
        this.setState({ [name]: checked }, () => {
            // switch (name) {
            //     case 'isCertified':
            //         if (!checked) {
            //             this.setState({ [name + 'Error']: "Please check certified checkbox." })

            //         } else {
            //             this.setState({ [name + 'Error']: '' })

            //         }
            //         break;
            //     default:
            //         break;
            // }
        });
    }

    onInputChangeHandler = (e) => {
        const { value, name } = e.target;
        this.setState({ [name]: value }, () => {
            switch (name) {
                case 'name':
                case 'zipCode':
                case 'inspectDate':
                case 'sellerSignatureDate':
                case 'ownerSignatureDate':
                case 'phone':
                case 'clearance':
                case 'selectedColor':
                case 'selectedBrandNumber':
                    if (value === '') {
                        this.setState({ [name + 'Error']: _.startCase(name) + " is requied" })

                    } else {
                        this.setState({ [name + 'Error']: '' })

                    }
                    break;
                case 'ownerName':
                    if (value === '') {
                        this.setState({ ownerNameError: "Owner name is requied", activeSuggestionOwner: false, })

                    } else {
                        const filteredSuggestions = this.state.ownerData.filter(
                            suggestion => suggestion.name.toLowerCase().indexOf(this.state.ownerName.toLowerCase()) > -1
                        );
                        this.setState({ ownerNameError: '', activeSuggestionOwner: true, filteredSuggestions, activeSuggestionBuyer: false })

                    }
                    break;
                case 'ownerAddress':
                    if (value === '') {
                        this.setState({ ownerAddressError: "Owner Address is requied" })

                    } else {
                        this.setState({ ownerAddressError: '', activeSuggestionOwner: false, activeSuggestionBuyer: false })

                    }
                    break;
                default:
                    break;
            }
        });
    }

    handleDeleteImage = (data) => {
        let photoGallery = [...this.state.photoGallery];
        photoGallery.map((item, index) => {
            if (item.id === data.id) {
                photoGallery.splice(index, 1);
            }
            return item;
        });
        this.setState({ photoGallery });
    }
    uploadMultipleFiles = (e) => {
        let fileObj = [];
        let fileArray = [...this.state.photoGallery];

        fileObj.push(e.target.files)

        for (let i = 0; i < fileObj[0].length; i++) {
            if (!fileObj[0][i].name.match(/\.(jpg|jpeg|png|gif)$/)) {
                this.setState({ invalidImage: 'Some image can not be upload' }, () => {
                    setTimeout(function () {
                        this.setState({ invalidImage: '' })
                    }.bind(this), 5000);
                });

            } else {
                fileArray.push({
                    id: Math.random(),
                    image: URL.createObjectURL(fileObj[0][i])
                })
            }

        }
        this.setState({ photoGallery: fileArray })
    }

    handleModal = () => {
        this.setState({ renderMarkModal: !this.state.renderMarkModal });
    }

    validateField = (name, value) => {
        switch (name) {
            case 'name':
            case 'zipCode':
            case 'inspectDate':
            case 'sellerSignatureDate':
            case 'ownerSignatureDate':
            case 'phone':
                if (value === '') {
                    this.setState({ [name + 'Error']: _.startCase(name) + " is requied" })
                    return false
                } else {
                    this.setState({ [name + 'Error']: '' })
                    return true;
                }

            case 'ownerName':
                if (value === '') {
                    this.setState({ ownerNameError: "Owner name is requied", activeSuggestionOwner: false, })
                    return false
                } else {
                    const filteredSuggestions = this.state.ownerData.filter(
                        suggestion => suggestion.name.toLowerCase().indexOf(this.state.ownerName.toLowerCase()) > -1
                    );
                    this.setState({ ownerNameError: '', activeSuggestionOwner: true, filteredSuggestions, activeSuggestionBuyer: false })
                    return true;
                }

            case 'ownerAddress':
                if (value === '') {
                    this.setState({ ownerAddressError: "Owner Address is requied" })
                    return false
                } else {
                    this.setState({ ownerAddressError: '', activeSuggestionOwner: false, activeSuggestionBuyer: false })
                    return true;
                }

            case 'isPermited':
                if (!value) {
                    this.setState({ [name + 'Error']: "Please check permited checkbox." })
                    return false
                } else {
                    this.setState({ [name + 'Error']: '' })
                    return true;
                }

            // case 'isCertified':
            //     if (!value) {
            //         this.setState({ [name + 'Error']: "Please check certified checkbox." })
            //         return false
            //     } else {
            //         this.setState({ [name + 'Error']: '' })
            //         return true;
            //     }

            case 'selectedBreed':
            case 'selectedBrand':
            case 'selectedSex':
            case 'selectedFaceMarking':
            case 'selectedMarkingLocation':
            case 'selectedLocation':
            case 'selectedCity':
            case 'selectedColor':
                if (value === '' || value === undefined) {
                    this.setState({ [name + 'Error']: _.startCase(name) + " is requied" })
                    return false
                } else {
                    this.setState({ [name + 'Error']: '' })
                    return true;
                }

            default:
                return true;

        }
    }

    isFormValid = (data) => {
        let isValid = true
        for (let [key, value] of Object.entries(data)) {
            let validation = this.validateField(key, value)
            if (isValid) {
                isValid = validation;
            }
        }
        return isValid
    }

    handleSave = () => {
        var url = window.location.href
            var arr = url.split("/");
            var locationUrl = arr[0] + "//" + arr[2]
        const data = {
            selectedBreed: this.state.selectedBreed,
            clearance: this.state.clearance,
            selectedBrand: this.state.selectedBrand,
            selectedBrandNumber: this.state.selectedBrandNumber,
            selectedSex: this.state.selectedSex,
            selectedFaceMarking: this.state.selectedFaceMarking,
            selectedMarkingLocation: this.state.selectedMarkingLocation,
            otherMarkingLocation: this.state.otherMarkingLocation,
            selectedLocation: this.state.selectedLocation,
            selectedCity: this.state.selectedCity,
            selectedState: this.state.selectedState,
            selectedColor: this.state.selectedColor,
            age: this.state.age,
            name: this.state.name,
            ownerName: this.state.ownerName,
            ownerAddress: this.state.ownerAddress,
            zipCode: this.state.zipCode,
            phone: this.state.phone,
            isPermited: this.state.isPermited,
            isAnimalUnbranderd: this.state.isAnimalUnbranderd,
            brandInspectorSign: this.state.brandInspectorSign,
            sellerOrAgentSign: this.state.sellerOrAgentSign,
            ownerSign: this.state.ownerSign,
            inspectDate: this.state.inspectDate,
            sellerSignatureDate: this.state.sellerSignatureDate,
            ownerSignatureDate: this.state.ownerSignatureDate,
            url: locationUrl + '/pre-sale-horse-permanent-travel-permit/edit/' 
        }
        console.log(data);
        

        const isValid = this.isFormValid(data);
        console.log(isValid);
        if (isValid) {
            console.log(data);
            
            // const url = getUrl("horse_permanent_travel_permit");
            // post(url, data)
            //     .then(res => {
            //         if (res.data.code === 200 && res.data.status) {
            //             this.props.postHorsePermanentTravelPermit(data);
            //             alert(res.data.message);
            //             setTimeout(function () {
            //                 window.location.reload(true);
            //             }, 5000);
            //         }
            //     })
            //     .catch(err => console.log(err));

        }
    }

    handleAutoComplete = (name, address) => {
        this.setState({ ownerName: name, ownerAddress: address, activeSuggestionOwner: false, ownerNameError: '', ownerAddressError: '' })
    }

    drawImage = (drawImage) => {
        this.setState({ drawImage })
    }
    clear = (ref) => {
        ref.clear()
    }

    trim = (signatureFor, ref) => {


        this.setState({
            [signatureFor]: ref.getTrimmedCanvas()
                .toDataURL('image/png')
        })
    }
    render() {
        
        return (
            <div className="row">
                <div className="col-lg-12">
                    <div className="panel-card-div pb-6" ref={el => (this.componentRef = el)}>
                        <div className="form-div">
                            <div className="row mr-minus-10">
                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Owner"
                                        value={this.state.ownerName}
                                        name="ownerName"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.activeSuggestionOwner === true && this.state.ownerName.length >= 1 &&
                                        <ul className="typeahead dropdown-menu" style={{ top: "46px", left: "10px", width: "221.328px", display: "block" }}>
                                            {this.state.filteredSuggestions.length > 0 ?
                                                this.state.filteredSuggestions.map((data, index) => {
                                                    return <li key={index} name="ownerName" onClick={() => this.handleAutoComplete(data.name, data.address)}>
                                                        <label>{data.name}</label>
                                                        <label className="address-link" href="#">{data.address}</label>
                                                    </li>
                                                }) :
                                                <li name="ownername" className="active" >
                                                    <label href="#">No Data Found!</label>
                                                </li>
                                            }
                                        </ul>
                                    }
                                    {this.state.ownerNameError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.ownerNameError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Address"
                                        value={this.state.ownerAddress}
                                        name="ownerAddress"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.ownerAddressError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.ownerAddressError}</div> : ''}
                                </div>

                            </div>
                            <div className="row mr-minus-10">
                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Choose City"
                                        value={this.state.selectedCity}
                                        handleChange={this.handleChangeCity}
                                        options={this.state.city} />
                                    {this.state.selectedCityError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '20px 0px 15px 0px' }}>{this.state.selectedCityError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Choose State"
                                        value={this.state.selectedState}
                                        handleChange={this.handleChangeState}
                                        options={this.state.stateList} />
                                    {this.state.selectedStateError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '20px 0px 15px 0px' }}>{this.state.selectedStateError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Zip code"
                                        value={this.state.zipCode}
                                        name="zipCode"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.zipCodeError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.zipCodeError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Phone"
                                        value={this.state.phone}
                                        name="phone"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.phoneError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.phoneError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Brand Number"
                                        value={this.state.selectedBrandNumber}
                                        name="selectedBrandNumber"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.selectedBrandNumberError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.selectedBrandNumberError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Choose Brand"
                                        value={this.state.selectedBrand}
                                        handleChange={this.handleChangeBrand}
                                        options={this.state.brand} />
                                    {this.state.selectedBrandError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.selectedBrandError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Location"
                                        value={this.state.selectedLocation}
                                        handleChange={this.handleChangeLocation}
                                        options={this.state.location} />
                                    {this.state.selectedLocationError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.selectedLocationError}</div> : ''}
                                </div>


                            </div>
                            <div className="row mr-minus-10">
                                <div className="col-lg-12 col-md-12 pr-10">
                                    <div className="custom-control custom-checkbox custom-checkbox-new checkbox-mtb-30">
                                        <input type="checkbox" className="custom-control-input" name="isAnimalUnbranderd" id="defaultUnchecked-permit" checked={this.state.isAnimalUnbranderd} onChange={this.onChangeChecked} />
                                        <label className="custom-control-label" htmlFor="defaultUnchecked-permit"> Check if Animal is Unbranded</label>
                                    </div>
                                </div>
                            </div>
                            <div className="row mr-minus-10">
                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Choose Sex"
                                        value={this.state.selectedSex}
                                        handleChange={this.handleChangeSex}
                                        options={this.state.sex} />
                                    {this.state.selectedSexError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.selectedSexError}</div> : ''}
                                </div>


                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Choose Face Marking"
                                        value={this.state.selectedFaceMarking}
                                        handleChange={this.handleChangeSelectedFaceMarking}
                                        options={this.state.facemarking} />
                                    {this.state.selectedFaceMarkingError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.selectedFaceMarkingError}</div> : ''}

                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Choose marking location"
                                        value={this.state.selectedMarkingLocation}
                                        handleChange={this.handleChangeSelectedMarkingLocation}
                                        options={this.state.markingLocation} />
                                    {this.state.selectedMarkingLocationError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.selectedMarkingLocationError}</div> : ''}

                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Other markings if have"
                                        value={this.state.otherMarkingLocation}
                                        handleChange={this.handleChangeOtherMarkingLocation}
                                        options={this.state.markingLocation} />
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Age"
                                        value={this.state.age}
                                        handleChange={this.handleChangeAge}
                                        options={this.state.optionAge} />
                                    {this.state.selectedAgeError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.selectedAgeError}</div> : ''}

                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Registered name"
                                        value={this.state.name}
                                        name="name"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.nameError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.nameError}</div> : ''}

                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Choose Breed "
                                        value={this.state.selectedBreed}
                                        handleChange={this.handleChangeBreed}
                                        options={this.state.breed} />
                                    {this.state.selectedBreedError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.selectedBreedError}</div> : ''}

                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Choose Color"
                                        value={this.state.selectedColor}
                                        handleChange={this.handleChangeColor}
                                        options={this.state.breed} />
                                    {this.state.selectedColorError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.selectedColorError}</div> : ''}

                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Clearance"
                                        value={this.state.clearance}
                                        name="clearance"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.clearanceError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.clearanceError}</div> : ''}
                                </div>
                            </div>
                            <div className="signature-area">
                                <div className="row mr-minus-10">
                                    <div className="col-lg-4 col-md-4 pr-10">
                                        <div className="form-group">
                                            <label>Signature of Owner</label>
                                            <SignatureCanvas penColor='black' canvasProps={{ "backgroundcolor": "#ffffff", className: "signautre-box" }} ref={(ref) => { this.ownerPad = ref }} />
                                            <button onClick={() => this.clear(this.ownerPad)} className="btn btn-clear mr-2">
                                                Clear
                                            </button>
                                            <button onClick={() => this.trim('ownerSign', this.ownerPad)} className="btn btn-savesign ">
                                                Save
                                            </button>
                                            <div className="row mr-minus-10">
                                                <div className="col-lg-10 col-md-12 pr-10">
                                                    <div className="form-group mt-20">
                                                        <div className="input-group datepicker-form">
                                                            <Flatpickr
                                                                options={{
                                                                    enableTime: false,
                                                                    altInput: true,
                                                                    altFormat: "F j, Y",
                                                                    dateFormat: "Y-m-d",
                                                                }}
                                                                onChange={date => {
                                                                    this.setState({ ownerSignatureDate: date, 'ownerSignatureDateError': '' });
                                                                }}
                                                                placeholder="Select Date"
                                                                value={this.state.ownerSignatureDate}
                                                            />
                                                            
                                                        </div>
                                                        {this.state.ownerSignatureDateError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '20px 0px 15px 0px' }}>{this.state.ownerSignatureDateError}</div> : ''}
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="col-lg-4 col-md-4 pr-10">
                                        <div className="form-group">
                                            <label>Signature of Seller/Agent</label>
                                            <SignatureCanvas penColor='black' canvasProps={{ "backgroundcolor": "#ffffff", className: "signautre-box" }} ref={(ref) => { this.sellerPad = ref }} />
                                            <button onClick={() => this.clear(this.sellerPad)} className="btn btn-clear mr-2">
                                                Clear
                                            </button>
                                            <button onClick={() => this.trim('sellerOrAgentSign', this.sellerPad)} className="btn btn-savesign">
                                                Save
                                            </button>

                                            <div className="row mr-minus-10">
                                                <div className="col-lg-10 col-md-12 pr-10">
                                                    <div className="form-group mt-20">
                                                        <div className="input-group datepicker-form">
                                                            <Flatpickr
                                                                options={{
                                                                    enableTime: false,
                                                                    altInput: true,
                                                                    altFormat: "F j, Y",
                                                                    dateFormat: "Y-m-d",
                                                                }}
                                                                onChange={date => {
                                                                    this.setState({ sellerSignatureDate: date, 'sellerSignatureDateError': '' });
                                                                }}
                                                                placeholder="Select Date"
                                                                value={this.state.sellerSignatureDate}
                                                            />
                                                            
                                                        </div>
                                                        {this.state.sellerSignatureDateError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '20px 0px 15px 0px' }}>{this.state.sellerSignatureDateError}</div> : ''}
                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                    </div>



                                    <div className="col-lg-4 col-md-4 pr-10">
                                        <div className="form-group">
                                            <label>Brand Inspector signature</label>
                                            <SignatureCanvas penColor='black' canvasProps={{ "backgroundcolor": "#ffffff", className: "signautre-box" }} ref={(ref) => { this.brandPad = ref }} />
                                            <button onClick={() => this.clear(this.brandPad)} className="btn btn-clear mr-2">
                                                Clear
                                            </button>
                                            <button onClick={() => this.trim('brandInspectorSign', this.brandPad)} className="btn btn-savesign">
                                                Save
                                            </button>
                                            <div className="row mr-minus-10">
                                                <div className="col-lg-10 col-md-12 pr-10">
                                                    <div className="form-group mt-20">
                                                        <div className="input-group datepicker-form">
                                                            <Flatpickr
                                                                options={{
                                                                    enableTime: false,
                                                                    altInput: true,
                                                                    altFormat: "F j, Y",
                                                                    dateFormat: "Y-m-d",
                                                                }}
                                                                onChange={date => {
                                                                    this.setState({ inspectDate: date, 'inspectDateError': '' });
                                                                }}
                                                                placeholder="Select Date"
                                                                value={this.state.inspectDate}
                                                            />
                                                            
                                                        </div>
                                                        {this.state.inspectDateError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '20px 0px 15px 0px' }}>{this.state.inspectDateError}</div> : ''}
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>


                                </div>
                            </div>
                        </div>
                    </div >
                    <div className="tab-footer tab-footer2">
                        <div className="row ">
                            <div className="col-lg-12 justify-content-end ">
                                <div className="right-side-footer right-side-footer-final d-flex d-flex-sm align-items-end justify-content-between">
                                    <div className="width-100 width-right d-flex sm-width-100">

                                        <div className="step-btn width-50 sm-width-auto order-md-1 order-2">
                                            
                                            <Link target="_blank" to="/pre-sale-print/45" className="btn btn-secondary2 btn-previous max-width-25"> <i className="fe fe-printer"></i> <span className="remove-mobile"> &nbsp; Print Permit</span></Link>
                                        </div>

                                        <div className="step-btn width-25 sm-width-50 pr-10 order-md-2 order-1">
                                            <button  className="btn btn-secondary btn-previous">Preview <span className="remove-mobile"> &nbsp; permit</span></button>
                                        </div>

                                        <div className="step-btn width-25 sm-width-50 order-md-3 order-3">
                                            <button  onClick={this.handleSave} className="btn btn-primary btn-save">Save</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div >
            </div >

        )
    }
}
